<template>
	<div class="page-container uncleStyle-container">
		<von-head>
			<div slot="left" class="back-arrow white" @click="$router.go(-1)"></div>
		</von-head>
		<div class="block-wrapper">
			<div class="block" v-for="block in blocks" :key="block.id" @click="onClickUncle(block.id)">
				<img :src="block.imgUrl" class="uncle-head" v-show="block.imgUrl">
				<p class="select-sign" v-show="block.imgUrl && block.selected">{{block.name}}登场</p>
				<div class="lock-layer" v-show="block.imgUrl && block.locked">
					<img src="https://vxiaocheng-jh.oss-cn-beijing.aliyuncs.com/H5_games/angryuncle/lock.png">
				</div>
			</div>
		</div>
		<div class="btn share" @click="onClickShare" v-if="false">邀请好友</div>
		<p class="tip" v-if="false">已连续登录<span>1</span>天、邀请<span>2</span>位好友</p>
	</div>
</template>

<script>
import {store, mutation} from '@/assets/js/store.js'
	
export default {
	data() {
		return {
			blocks: []
		}
	},

	computed: {
		curUncleId() {
			return store.curUncleId
		}
	},

	methods: {
		onClickUncle(uncleId) {
			const block = this.blocks.find(e => e.id === uncleId)

			if (block.locked || !block.imgUrl) return;

			this.selectUncle(uncleId)
		},

		onClickShare() {
			console.log('邀请好友')
			
			console.log('this.curUncleId', this.curUncleId)
		},

		selectUncle(uncleId = 1) {
			const block = this.blocks.find(e => e.id === uncleId)

			this.blocks.find(e => e.selected) ? this.blocks.find(e => e.selected).selected = false : ''
			block.locked = false
			block.selected = !block.locked && true

			mutation.setUncleId(uncleId)
		},

		resetBlocks() {
			let newArray = Array.from(new Array(9))

			this.blocks = newArray.map((e, i) => e = {
				id: ++i,
				name: i == 5 ? '单身狗' : i == 6 ? '猪猪女孩' : '大叔',
				imgUrl: i > 6 ? '' : `https://vxiaocheng-jh.oss-cn-beijing.aliyuncs.com/H5_games/angryuncle/uncle_head_${i}.png`,
				locked: false,
				selected: false
			})
		}
	},

	created() {
		this.resetBlocks()
		this.selectUncle(this.curUncleId)
	}
}
</script>