<style lang="scss">
.angryUncle-container {}
</style>

<style lang="scss" scoped>
.angryUncle-container {
	background-image: url(https://vxiaocheng-jh.oss-cn-beijing.aliyuncs.com/H5_games/angryuncle/bg.png);

	.box-wrapper {
		background: url(https://vxiaocheng-jh.oss-cn-beijing.aliyuncs.com/H5_games/angryuncle/box.png) no-repeat 0 0 / 100% 100%;
		height: 133.8667vw;
		margin-left: calc(100vw - 98.13333vw);
		position: relative;
		width: 98.13333vw;

		&.shaking {
			animation: shaking .2s linear 6;
		}

		.head-wrapper {
			display: flex;
			flex-wrap: wrap;
			height: 100%;
			transition: .8s;
			width: 100%;

			&.enter {
				transform: translateY(0);
			}

			&.leave {
				transform: translateY(-120%);
			}

			.head-view {
				height: 32.53333vw;
				margin-left: -2.66667vw;
				width: 26.4vw;

				&.leave {
					animation: leave .8s linear forwards;
				}

				&:nth-child(4n + 1) {
					margin-left: 0;
				}

				@for $i from 5 to 17 {
					@if $i < 9 {
						&:nth-child(#{$i}) {
							transform: translateY(-2.66667vw);
						}
					}
					@else if $i < 13 {
						&:nth-child(#{$i}) {
							transform: translateY(-5.33334vw);
						}
					}
					@else if $i < 17 {
						&:nth-child(#{$i}) {
							transform: translateY(-8vw);
						}
					}
				}

				img {
					height: 100%;
					width: 100%;
				}
			}
		}

		.uncle-selected {
			height: 133.8667vw;
			left: calc(98.13333vw - 100vw);
			position: absolute;
			top: 0;
			width: 100vw;
			z-index: 9;

			img {
				max-width: 100%;
			}
		}
	}

	.change-style {
		background: url(https://vxiaocheng-jh.oss-cn-beijing.aliyuncs.com/H5_games/angryuncle/change_uncle.png) no-repeat 0 0 / 100% 100%;
		height: 21.8666vw;
		margin: 0 auto;
		width: 49.1vw;
	}

	.btns {
		align-items: center;
		display: flex;
		justify-content: center;
		margin: 5.3333vw auto 0;

		.btn {
			border: 3px solid #000;
			border-radius: 8vw;
			box-shadow: -.3rem -.2rem 0 0 rgba(0,0,0,.15) inset;
			color: #fff;
			font-size: 19px;
			font-weight: bold;
			height: 14.93333vw;
			line-height: calc(14.93333vw - 6px);
			text-align: center;
			width: 40vw;

			&.again {
				background-color: #7F3D00;
				color: #fff;
			}

			&.punishment {
				background-color: #fff;
				color: #000;
				margin-left: 3.7777vw;
			}
		}
	}
}

@keyframes leave {
	from {
		transform: rotate(180deg) translate3d(0,0,0);
	}

	to {
		transform: rotate(180deg) translate3d(0,200vw,0);
	}
}

@keyframes shaking {
	0% {
		transform: translateX(0);
	}

	33% {
		transform: translateX(-3px);
	}

	66% {
		transform: translateX(3px);
	}

	100% {
		transform: translateX(0);
	}
}
</style>

<template>
	<div class="page-container angryUncle-container">
		<von-head v-if="false">
			<div slot="left" class="back-arrow white"></div>
			<div slot="middle" class="title white">喝酒神器</div>
		</von-head> 
		<div :class="['box-wrapper', punishTa && 'shaking']">
			<div :class="['head-wrapper', punishTa && 'leave']">
				<div :class="['head-view', head.isLeave && 'leave']" v-for="head in heads" :key="head.id" @click="onClickHead(head.id)">
					<img :src="`https://vxiaocheng-jh.oss-cn-beijing.aliyuncs.com/H5_games/angryuncle/uncle_head_${head.isLeave ? 'dark_' : ''}${curUncleId}.png`" alt="">
				</div>
			</div>
			<div class="uncle-selected" v-show="punishTa">
				<img :src="`https://vxiaocheng-jh.oss-cn-beijing.aliyuncs.com/H5_games/angryuncle/uncle_selected_${curUncleId}_1.png`" v-show="uncleSelectTab">
				<img :src="`https://vxiaocheng-jh.oss-cn-beijing.aliyuncs.com/H5_games/angryuncle/uncle_selected_${curUncleId}_2.png`" v-show="!uncleSelectTab">
			</div>
		</div>
		<div class="change-style" v-show="!punishTa" @click="onClickChangeStyle"></div>
		<div class="btns" v-show="punishTa">
			<div class="btn again" @click="onClickAgain">再来一局</div>
			<div class="btn punishment" @click="onClickPunish">选择惩罚</div>
		</div>
	</div>
</template>

<script>
import {store} from '@/assets/js/store.js'
	
export default {
	name: 'AngryUncle',
	data() {
		return {
			heads: [],
			punishTa: false,
			uncleSelectTab: true
		}
	},

	computed: {
		curUncleId() {
			return store.curUncleId
		}
	},

	methods: {
		onClickHead(headId) {
			let head = this.heads.find(head => head.id === headId)
			if (head.isAngryUncle) {
				this.UncleSelected()
				return;
			}
			head.isLeave = true
		},

		UncleSelected() {
			this.punishTa = true
			let i = 0

			const tempround = setInterval(() => {
				this.uncleSelectTab = !this.uncleSelectTab
				if (i++ > 6) {
					clearInterval(tempround)
				}
			}, 300)
		},

		onClickChangeStyle() {
			this.$router.push({
				path: '/UncleStyle'
			})
		},

		onClickAgain() {
			this.punishTa = false
			this.resetHeads()
		},

		onClickPunish() {
			this.$router.push({
				path: '/Punishment'
			})
		},

		resetHeads() {
			let newArray = Array.from(new Array(16))
			const index = Math.floor(Math.random() * 16)

			this.heads = newArray.map((e, i) => e = {
				id: i,
				isAngryUncle: i === index,
				isLeave: false
			})
		}
	},

	created() {
		// 初始化 heads
		this.resetHeads()
	}
}
</script>