import Vue from 'vue'
import Router from 'vue-router'
import AngryUncle from './views/AngryUncle.vue'
import UncleStyle from './views/UncleStyle.vue'
import Punishment from './views/punishment/Punishment.vue'

Vue.use(Router)

export default new Router({
	mode: 'hash',
	base: process.env.BASE_URL,
	routes: [
		{
			path: '/',
			name: 'AngryUncle',
			component: AngryUncle
		},
		{
			path: '/UncleStyle',
			name: 'UncleStyle',
			component: UncleStyle
		},
		{
			path: '/Punishment',
			name: 'Punishment',
			component: Punishment
		}
	]
})
