import Vue from 'vue'

let ls_curUncleId = localStorage.getItem('curUncleId') && Number(localStorage.getItem('curUncleId')) || 1

export const store = Vue.observable({
	curUncleId: ls_curUncleId
})

export const mutation = {
	setUncleId(uncleId) {
		store.curUncleId = uncleId
		localStorage.setItem('curUncleId', uncleId)
	}
}